import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest, handleRequest, service }) => ({
  /**
   * @name Intents#showProductAliasForm
   * @description The intent to show the product details form.
   * @type {function}
   */
  showProductAliasForm: createRequest(Intent.SHOW_PRODUCT_ALIAS_FORM),

  /**
   * @name Intents#showProductDetailsView
   * @description Handler of the 'intent.bb.productDetails.view.show' intent.
   * @type {function}
   */
  showProductDetailsView: handleRequest(Intent.SHOW_PRODUCT_DETAILS_VIEW, productId => {
    if (productId) {
      service.loadProductDetailsIfNeeded(productId);
    } else {
      throw new Error(`productId is undefined.
        Please make sure to pass a productId as the payload of the intent.`);
    }
  }),
});



