import productDetailsView from './product-details-view';

/**
 * @description
 * Helpers for ext-bbm-product-details-view-ng
 *
 * @name Helpers
 * @type {Object}
 */
export default ({ stateContainer }) => {
  /**
   * @name Helpers#processProductDetails
   * @description
   * Hook for processing product details.
   * @type {function}
   *
   * @param {module:model-bb-product-summary-ng.ProductDetails} productDetails
   * @returns {object}
   * @inner
   */
  const processProductDetails = productDetails => productDetailsView(productDetails);

  /**
   * @name Helpers#getProductDetailsState
   * @description Returns the product details state.
   * @type {function}
   *
   * @inner
   */
  const getProductDetailsState = stateContainer.createSelector(state =>
    state.productDetails
  );

  /**
   * @name Helpers#getProductDetails
   * @description Returns product details data.
   * @type {function}
   */
  const getProductDetails = stateContainer.createSelector(state => (
    state.productDetails.data ?
      processProductDetails(state.productDetails.data) : null
  ));

  /**
   * @name Helpers#hasProductDetails
   * @description Checks if product details is loaded.
   * @type {function}
   *
   * @returns {boolean}
   */
  const hasProductDetails = () => (
    Boolean(getProductDetailsState().data)
  );

  /**
   * @name Helpers#showLoadingState
   * @description Checks for the loading.
   * @type {function}
   *
   * @returns {boolean}
   */
  const showLoadingState = () => (
    getProductDetailsState().loading &&
    !hasProductDetails()
  );

  /**
   * @name Helpers#showErrorState
   * @description Checks for the error.
   * @type {function}
   *
   * @returns {boolean}
   */
  const showErrorState = () => (
    getProductDetailsState().error &&
    !getProductDetailsState().loading
  );

  return {
    hasProductDetails,
    showLoadingState,
    showErrorState,
    getProductDetails,
  };
};



